body {
    font-family: 'Arial', sans-serif;
    background-color: #f9f9f9;
    color: #333;
    margin: 0;
    padding: 0;
}

.intro {
    padding: 20px;
    display: flex;
    justify-content: center;
}

.content-wrapper {
    width: 100%;
    max-width: 1200px;
    padding-left: 20px;
    padding-right: 20px;
}

.intro-section-name-picture {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 20px;
    justify-content: flex-start; /* Align to the right for large screens */
}

.intro-name {
    display: flex;
    flex-direction: column;
    margin-right: 20px;
    align-items: flex-start;
}

.intro-name h1 {
    font-size: 2em; /* Adjust as needed */
    color: #4F0E2D; /* Dark purple color */
    margin: 0; /* Remove default margins */
    line-height: 1.1; /* Adjust line height to bring the names closer */
}

.profile-image-container {
    position: relative;
    display: inline-block;
    margin-bottom: 20px;
}

.profile-image {
    border-radius: 50%;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border: 5px solid #4F0E2D; /* Dark purple solid border */

}

.intro-section-about-me {
    text-align: left;
    max-width: 800px;
    margin: 0;
    padding-left: 0; /* Remove left padding */
    margin-top: -80px; /* Move the about me section up */
}

.intro-section-about-me h2 {
    font-size: 1.75em;
    color: #4F0E2D;
    margin-top: 10px;
    margin-bottom: 15px;
    padding-bottom: 5px;
    display: inline-block;
    border-bottom: 2px solid #4F0E2D;
}

.intro-container {
    color: #333; /* Dark grey text color */
    line-height: 1.8;
    margin-bottom: 15px;
    text-align: justify;
}

@media (max-width: 1200px) {
    .content-wrapper {
        width: 95%;
        padding-left: 2.5%;
        padding-right: 2.5%;
    }

    .intro-section-name-picture {
        justify-content: center; /* Center align for medium screens */
    }

    .intro-section-about-me {
        margin-top: 20px; /* Reset margin top for medium screens */
    }
}

@media (max-width: 768px) {
    .content-wrapper {
        width: 95%;
        padding-left: 5%; /* Adjust for smaller screens */
        padding-right: 5%; /* Adjust for smaller screens */
    }

    .intro-section-name-picture {
        flex-direction: column;
        align-items: flex-start;
    }

    .intro-name {
        margin-right: 0;
        margin-bottom: 10px; /* Add some space between name and picture */
        align-items: flex-start;
    }

    .intro-name h1 {
        text-align: left;
    }

    .profile-image {
        width: 80%; /* Adjust width for smaller screens */
    }

    .intro-section-about-me {
        text-align: left;
        padding-left: 5%; /* Adjust for smaller screens */
        margin-top: 0; /* Reset margin top for small screens */
    }
}
