.education-page {
    padding: 20px;
    display: flex;
    justify-content: center;
}

.education-section {
    text-align: left;
}

.education-section h2 {
    font-size: 1.6em;
    color: #4F0E2D; /* Dark purple color */
    margin-bottom: 20px;
    border-bottom: 2px solid #4F0E2D;
    display: inline-block;
}

.education-details {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    margin-top: 20px;
}

.mff-image {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    width: 200px; /* Adjust size as needed */
    height: auto;
    margin-right: 20px;
    border-radius: 50%;
}

.education-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.education-info h3 {
    font-size: 1.4em;
    margin: 0;
    color: #4F0E2D; /* Dark purple color */
    margin-bottom: 10px; /* Add spacing below the heading */
}

.university {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    font-size: 1.2em;
    margin-bottom: 10px;
}

.university-name {
    font-weight: bold;
}

.university-location {
    font-weight: normal;
    white-space: nowrap; /* Prevent line break within location */
    padding-left: 2px;
}

.education-date, .major-details {
    margin: 5px 0;
    line-height: 1.2; /* Improve readability */
    font-size: 1.1em;
    padding-left: 20px; /* Indentation for subpoints */
    font-style: italic;
}

.thesis {
    margin-top: 10px;
    padding: 10px;
}

@media (max-width: 768px) {
    .education-details {
        flex-direction: column;
        align-items: center;
    }

    .mff-image {
        margin-left: 5px;
        margin-bottom: 20px;
        max-width: 150px;
    }

    .education-date, .major-details {
        padding-left: 0; /* Remove indentation for smaller screens */
    }
}
