.experience-page {
    padding: 20px;
    display: flex;
    justify-content: center;
}

.experience-section {
    text-align: left;
}

.experience-section h2 {
    font-size: 1.6em;
    color: #4F0E2D; /* Dark purple color */
    margin-bottom: 20px;
    border-bottom: 2px solid #4F0E2D;
    display: inline-block;
}

.experience-details {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    margin-top: 20px;
}

.company-image {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    width: 200px; /* Adjust size as needed */
    height: auto;
    margin-left: 20px;
    border-radius: 5%;
}

.experience-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.experience-info h3 {
    font-size: 1.4em;
    margin: 0;
    color: #4F0E2D; /* Dark purple color */
    margin-bottom: 10px; /* Add spacing below the heading */
}

.company {
    font-size: 1.2em;
    margin-bottom: 10px;
}

.company-name {
    font-weight: bold;
}

.company-location {
    display: block;
    font-weight: normal;
    white-space: nowrap; /* Prevent line break within location */
}

.experience-date, .role-details {
    margin: 5px 0;
    line-height: 1.2; /* Improve readability */
    font-size: 1.1em;
    padding-left: 20px;
    font-style: italic;
}

.description {
    margin-top: 10px;
    padding: 10px;
}

@media (max-width: 768px) {
    .experience-details {
        flex-direction: column;
        align-items: center;
    }

    .company-image {
        margin-left: 5px;
        margin-bottom: 20px;
        max-width: 150px;
    }

    .experience-date, .role-details {
        padding-left: 0; /* Remove indentation for smaller screens */
    }
}
